.table {
    width: 100%;
    border-collapse: collapse;
    margin-bottom: 1rem;
  }
  
  .table th,
  .table td {
    padding: 0.75rem;
    vertical-align: top;
  }
  
  .table th {
    border-top: 1px solid #dee2e6;
    text-align: left;
  }
  
  .table td {
    border-top: 1px solid #dee2e6;
  }
  
  .message-column {
    width: 25%; /* Adjust the width as needed */
  }
  
  .sentAt-column {
    width: 15%; /* Adjust the width as needed */
  }
  
  .heading {
    text-align: center;
    margin-bottom: 1.5rem;
    font-size: 1.5rem;
    color: #333;
  }
  